body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.App{text-align:center}

nav{height:40px;width:100vw;background-color:#282c34;z-index:1;position:fixed;transition:300ms ease all}nav .navbar-list{display:flex;padding:0;padding-left:10px;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0;height:100%;align-items:center}nav .navbar-list figure{width:15px;margin:0;padding:10px;cursor:pointer}nav .navbar-list figure .visible{position:relative;bottom:5px;border:solid white;border-width:0 3px 3px 0;display:inline-block;padding:3px;transform:rotate(45deg);-webkit-transform:rotate(45deg)}nav .navbar-list figure .hidden{border:solid white;border-width:0 3px 3px 0;display:inline-block;padding:3px;transform:rotate(-135deg);-webkit-transform:rotate(-135deg)}nav .navbar-list .navbar-element{transition:250ms ease all;align-items:center;border-radius:2px;padding:5px 8px 8px 5px;list-style-type:none;cursor:pointer}nav .navbar-list .navbar-link{color:white;text-decoration:none;padding:5px}nav.navbar-hidden{height:20px}nav.navbar-hidden .navbar-element{display:none}nav.navbar-transparent{opacity:0.70}@media screen and (max-width: 759px){nav .navbar-list{padding-left:3px}nav .navbar-list .navbar-element{padding:3px}nav .navbar-list .navbar-link{padding:3px;font-size:1em}}

.App-Welcome{display:flex;height:100vh}.App-Welcome .Welcome-Image{-webkit-user-select:none;-ms-user-select:none;user-select:none;max-width:100%;object-fit:cover;z-index:-1}.App-Welcome .Welcome-Tran{background-color:black;border-radius:300px;z-index:0;opacity:0.03;width:420px;height:100px;position:absolute;left:calc(50vw - 210px);top:calc(50vh - 107px)}.App-Welcome .Welcome{display:inline-block;width:450px;position:absolute;left:calc(50vw - 225px);top:calc(50vh - 100px);color:white;font-size:1em}.App-Welcome .Welcome h1,.App-Welcome .Welcome h2{margin:0;margin-bottom:5px;text-shadow:0 0 10px black}@media screen and (max-width: 600px){.App-Welcome .Welcome{width:300px;left:calc(50vw - 150px)}.App-Welcome .Welcome-Tran{width:300px;height:120px;left:calc(50vw - 150px);top:calc(50vh - 101px)}}

.App-main{background-color:#282c34;color:#CDCDCD;display:flex;padding:1em;height:100vh;color:#CDCDCD}.App-main .profile{margin:auto;padding:0.5em}.App-main .profile img{width:37vw;border-radius:50%}.App-main .intro{margin:auto;max-width:52vw;padding:1em;border-style:solid;border-color:#CDCDCD;border-radius:1em}@media screen and (max-width: 759px){.App-main{flex-direction:column;align-items:center}.App-main .profile{margin:0;margin-top:5vh}.App-main .profile img{max-width:475px;width:90%}.App-main .intro{margin:0;margin-top:10px;max-width:90%}.App-main img{width:55vw}}

.App-Experiences{background-color:#CDCDCD;padding-bottom:25px}.App-Experiences .Experiences-Title{padding-top:15px}.App-Experiences .Experience-Box{transition:200ms ease all;background-color:white;margin:auto;margin-bottom:10px;display:flex;text-align:left;width:85vw;height:300px;box-shadow:0 3px 3px #282c34;border-radius:0.15em}.App-Experiences .Experience-Box .Experience-Info{display:flex;flex-direction:column;justify-content:center;width:50%;margin-left:10px;padding:10px;padding-right:0}.App-Experiences .Experience-Box .Experience-Info h2,.App-Experiences .Experience-Box .Experience-Info h4{margin-bottom:0}.App-Experiences .Experience-Box .Experience-Info #subtitle{margin:0;text-align:left}.App-Experiences .Experience-Box .Experience-Info h2,.App-Experiences .Experience-Box .Experience-Info h4,.App-Experiences .Experience-Box .Experience-Info p{margin-right:10px}.App-Experiences .Experience-Box .Experience-Image{padding:10px;padding-left:0px;width:50%}.App-Experiences .Experience-Box .Experience-Image img{border-radius:0.10em;object-fit:cover;width:100%;height:100%}.App-Experiences .Experience-Box:hover{box-shadow:0 7px 7px #282c34;width:calc(85vw + 5px)}.App-Experiences .flipped{flex-direction:row-reverse}.App-Experiences .flipped .Experience-Image{padding-left:10px;padding-right:0}.App-Experiences .flipped .Experience-Info{padding-left:0;padding-right:10px}.App-Experiences .flipped .Experience-Info h2,.App-Experiences .flipped .Experience-Info h4,.App-Experiences .flipped .Experience-Info p{margin-right:0}@media screen and (max-width: 640px){.App-Experiences .Experience-Box{width:98vw;height:500px}.App-Experiences .Experience-Box .Experience-Info{padding:3px;align-items:center}.App-Experiences .Experience-Box .Experience-Image{display:flex;flex-direction:column;justify-content:center;width:55vw;padding:3px}.App-Experiences .Experience-Box .Experience-Image img{object-fit:cover;width:100%;height:100%}.App-Experiences .Experience-Box:hover{width:calc(98vw + 3px)}}

.App-Projects{background-color:#282c34;padding-bottom:35px}.App-Projects .Projects-Title{padding-top:15px;color:#CDCDCD}.App-Projects .Projects-Title h1{margin-bottom:10px}.App-Projects .Projects-All{margin:auto;width:95vw;display:grid;grid-template-columns:repeat(auto-fit, 390px);grid-column-gap:0.5em;grid-row-gap:0.5em;grid-auto-rows:550px;justify-content:center}.App-Projects .Projects-All .Project-Box{transition:300ms ease all;background-color:#CDCDCD;border-radius:3px;box-shadow:0 3px 3px #282c34;margin:1em 1.35em 1em 1.35em;padding:5px;text-align:left}.App-Projects .Projects-All .Project-Box .Project-Image{height:50%}.App-Projects .Projects-All .Project-Box .Project-Image img{object-fit:cover;width:100%;height:100%}.App-Projects .Projects-All .Project-Box .Project-Title a{text-decoration:none;color:#282c34}.App-Projects .Projects-All .Project-Box .Project-Title h4{font-size:22px;margin:5px;margin-left:0;margin-bottom:0}.App-Projects .Projects-All .Project-Box .Project-Title h6{font-size:15px;margin-top:0;margin-bottom:2px}.App-Projects .Projects-All .Project-Box .Project-Title p{font-size:13px;margin:7px 0 0 0}.App-Projects .Projects-All .Project-Box .Project-Info p{margin:3px 5px 0 0}.App-Projects .Projects-All .Project-Box:hover{position:relative;z-index:0;box-shadow:0 9px 9px #282c34}@media screen and (max-width: 400px){.App-Projects .Projects-All{grid-template-columns:repeat(auto-fit, 340px);grid-row-gap:0.1em}.App-Projects .Projects-All .Project-Box{margin:0.5em 1em 0.5em 1em}}

.App-Contact .Contact-Links a{padding:25px;font-size:3em;color:black}

